.App {
  text-align: center;
  background-color: #A6CFE2;
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#calc-div{
  background-color: #0B0C11;
  height: 420px;
  width: 300px;
  padding: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-template-rows: auto auto auto auto auto auto;
  -webkit-box-shadow: 2px 2px 10px 2px #81814a;
          box-shadow: 2px 2px 10px 2px #81814a;
  border-radius: 5px;
}

#display{
  grid-column: 1 / 5;
  grid-row: 1;
  color: #ffffff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding-bottom: 10px;
  font-size: 26px;
}

.buttons{
  color: #ffffff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border: 1px solid #000000;
  border-radius: 5px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  opacity: 0.9;
}

.buttons:hover{
  border: 1px solid #cccccc;
  opacity: 1;
}

#clear{
  grid-column: 1 / 3;
  grid-row: 2;
  background-color: #AC3939;
}

#divide{
  grid-column: 3;
  grid-row: 2;
  background-color: #666666;
}

#multiply{
  grid-column: 4;
  grid-row: 2;
  background-color: #666666;
}

#seven{
  grid-column: 1;
  grid-row: 3;
  background-color: #4D4D4D;
}

#eight{
  grid-column: 2;
  grid-row: 3;
  background-color: #4D4D4D;
}

#nine{
  grid-column: 3;
  grid-row: 3;
  background-color: #4D4D4D;
}

#subtract{
  grid-column: 4;
  grid-row: 3;
  background-color: #666666;
}

#four{
  grid-column: 1;
  grid-row: 4;
  background-color: #4D4D4D;
}

#five{
  grid-column: 2;
  grid-row: 4;
  background-color: #4D4D4D;
}

#six{
  grid-column: 3;
  grid-row: 4;
  background-color: #4D4D4D;
}

#add{
  grid-column: 4;
  grid-row: 4;
  background-color: #666666;
}

#one{
  grid-column: 1;
  grid-row: 5;
  background-color: #4D4D4D;
}

#two{
  grid-column: 2;
  grid-row: 5;
  background-color: #4D4D4D;
}

#three{
  grid-column: 3;
  grid-row: 5;
  background-color: #4D4D4D;
}

#equals{
  grid-column: 4;
  grid-row: 5 / 7;
  background-color: #004466;
}

#zero{
  grid-column: 1 / 3;
  grid-row: 6;
  background-color: #4D4D4D;
}

#decimal{
  grid-column: 3;
  grid-row: 6;
  background-color: #4D4D4D;
}


